.btn {
  border-radius: 0.25rem;
  font-family: $font-family-sans-serif;
  font-weight: 600;
  min-width: 6rem;

  &.btn-lg {
    min-width: 12rem;
  }
}

button:focus {
  outline: none;
}

.btn.btn-outline-light:hover {
  color: theme-color('primary-dark');
}

.btn.btn-outline-light-success:hover {
  color: theme-color('success');
}

