// Front Page
section.page-header-jumbotron .jumbotron {
  padding-top: 6rem;
  padding-bottom: 4rem;

  @include media-breakpoint-up(lg) {
    padding: 8rem 2rem;
  }
}

section.about-jumbotron .jumbotron,
section.persona-type-jumbotron .jumbotron,
section.product-jumbotron .jumbotron,
section.front-page-jumbotron .jumbotron {
  padding: 4rem 0;

  @include media-breakpoint-up(lg) {
    padding: 6rem 0 8rem;
  }
}

section.about-jumbotron {
  h1 {
    line-height: 3rem;
  }
  .lead {
    color: $gray-500;
  }
}

section.front-page-jumbotron .jumbotron h1 {
    @include media-breakpoint-up(lg) {
      font-size: 4rem;
      line-height: 5rem;

    }
}

section.persona-type-jumbotron .jumbotron h1 {
  @include media-breakpoint-up(lg) {
    font-size: 3rem;
  }
}

.drupalcon-logo-wrapper {
  position: relative;
}

.drupalcon-cta {
  position: relative;
  top: 0.25rem;
}

section.front-page-recent-posts {
  @include media-breakpoint-up(lg) {
    padding: 4rem 0;
  }
}

section.front-page-tweets .card-deck {
  margin: 0 auto;
}

// Landing Pages

body.page {
  .coming-soon {
    position: relative;
    &:before {
      content: "Coming Soon";
      background: rgba($color: #000000, $alpha: 0.8);
      z-index: 20;
      border-radius: 0.25rem;
      color: $white;
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      height: 100%;
      width: 100%;
    }
  }
  .more-to-come {
    position: relative;
    &:before {
      content: "Laravel, Symfony, and other PHP applications coming soon!";
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      height: 100%;
      padding: 5rem;
    }
  }
  .rounded-circle {
    height: 30px;
    width: 30px;
    display: inline-block;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;
  }
  .landing-open-source {
    .rounded-circle {
      height: 40px;
      width: 40px;
      line-height: 35px;
      i {
        font-size: 1rem;
        vertical-align: middle;
      }
    }
    h2 > small {
      font-size: 0.8rem;
    }
  }
}

body.page .main {
  min-height: 75vh;
}
body.display-header .main {
  margin-top: $navbar-brand-height * 2.5;
}
